* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}
#country_item {
  list-style: none;
  font-size: 24px;
  font: Arial;
}
.text_two {
  display: flex;
  font-size: 32px;
  margin-bottom: 20px;
}
.data {
  font-size: 18px;
  margin-bottom: 10px;
}
.text_data {
  font-family: 'Roboto';
  font-weight: bold;
  margin-right: 15px;
  font-size: 20px;
}
.flag {
  margin-left: 15px;
  margin-right: 15px;
  width: 25px;
  height: 25px;
}
